/*  === FONTS   === */

@font-face {
    font-family: 'OldKingC';
    src: url('../fonts/OldKingC.eot');
    src: url('../fonts/OldKingC.eot?#iefix') format('embedded-opentype'),
        url('../fonts/OldKingC.woff2') format('woff2'),
        url('../fonts/OldKingC.woff') format('woff'),
        url('../fonts/OldKingC.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'OldKingC';
    src: url('../fonts/OldKingC-Bold.eot');
    src: url('../fonts/OldKingC-Bold.eot?#iefix') format('embedded-opentype'),
        url('../fonts/OldKingC-Bold.woff2') format('woff2'),
        url('../fonts/OldKingC-Bold.woff') format('woff'),
        url('../fonts/OldKingC-Bold.ttf') format('truetype');
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'Sunflower';
    src: local('Sunflower Light'), 
    local('Sunflower-Light'), 
        url('../fonts/Sunflowerlight.woff2') format('woff2'), 
        url('../fonts/Sunflowerlight.woff') format('woff'), 
        url('../fonts/Sunflowerlight.ttf') format('truetype');
    font-weight: 300;
    font-style: normal;
}
@font-face {
    font-family: 'Sunflower';
    src: local('Sunflower Medium'), 
        local('Sunflower-Medium'), 
        url('../fonts/Sunflowermedium.woff2') format('woff2'), 
        url('../fonts/Sunflowermedium.woff') format('woff'), 
        url('../fonts/Sunflowermedium.ttf') format('truetype');
    font-weight: 500;
    font-style: normal;
}
@font-face {
    font-family: 'Sunflower';
    src: local('Sunflower Bold'), 
         local('Sunflower-Bold'), 
         url('../fonts/Sunflowerbold.woff2') format('woff2'), 
         url('../fonts/Sunflowerbold.woff') format('woff'),
         url('../fonts/Sunflowerbold.ttf') format('truetype');
    font-weight: 700;
    font-style: normal;
}

@font-face {
  font-family: 'icomoon';
  src:  url('../fonts/icomoon.eot?rpzfpg');
  src:  url('../fonts/icomoon.eot?rpzfpg#iefix') format('embedded-opentype'),
    url('../fonts/icomoon.ttf?rpzfpg') format('truetype'),
    url('../fonts/icomoon.woff?rpzfpg') format('woff'),
    url('../fonts/icomoon.svg?rpzfpg#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

