@import "_variables.scss";

@media only screen and (min-width: 1160px){
	.hot-offers{
		.hot-offers__slider{
			transform: translateY(50px);
			opacity: 0;
			transition: 0.5s;
		}

		&.animated{
			.hot-offers__slider{
				transform: translateY(0px);
				opacity: 1;
			}
		}
	}

	.top-sale{
		.owl-item{
			&:nth-of-type(4),
			&:nth-of-type(5),
			&:nth-of-type(6){
				opacity: 0;
				transform: translateY(50px);
			}

			&:nth-of-type(4){
				transition: all 0.5s ease-out 0s;
			}

			&:nth-of-type(5){
				transition: all 0.5s ease-out 0.25s;
			}

			&:nth-of-type(6){
				transition: all 0.5s ease-out 0.5s;
			}
		}

		&.animated{
			.owl-item{
				&:nth-of-type(4),
				&:nth-of-type(5),
				&:nth-of-type(6){
					opacity: 1;
					transform: translateY(0px);
				}
			}
		}
	}

	.choose-us{
		.choose-left{
			opacity: 0;
			transform: translateX(-50px);
			transition: all 0.5s ease-out 0s;
		}

		.choose-right-top{
			opacity: 0;
			transform: translateX(50px);
			transition: all 0.5s ease-out 0.25s;
		}

		.choose-right-bottom{
			opacity: 0;
			transform: translateY(50px);
			transition: all 0.5s ease-out 0.5s;
		}


		&.animated{
			.choose-left{
				opacity: 1;
				transform: translateX(0px);
			}

			.choose-right-top{
				opacity: 1;
				transform: translateX(0px);
			}

			.choose-right-bottom{
				opacity: 1;
				transform: translateY(0px);
			}
		}
	}

	.publications{
		.public-col{
				&:nth-of-type(1){
					opacity: 0;
					transform: translateX(-50px);
					transition: all 0.5s ease-out 0.25s;
				}

				&:nth-of-type(2){
					opacity: 0;
					transform: translateX(50px);
					transition: all 0.5s ease-out 0.5s;
				}
			}

		&.animated{
			.public-col{
				opacity: 1;
				transform: translateX(0px);
			}
		}
	}

	.reviews{
		.owl-item{
			&:nth-of-type(5),
			&:nth-of-type(6),
			&:nth-of-type(7),
			&:nth-of-type(8){
				opacity: 0;
				transform: translateY(50px);
			}

			&:nth-of-type(5){
				transition: all 0.5s ease-out 0s;
			}

			&:nth-of-type(6){
				transition: all 0.5s ease-out 0.25s;
			}

			&:nth-of-type(7){
				transition: all 0.5s ease-out 0.5s;
			}

			&:nth-of-type(8){
				transition: all 0.5s ease-out 0.75s;
			}
		}

		&.animated{
			.owl-item{
				&:nth-of-type(5),
				&:nth-of-type(6),
				&:nth-of-type(7),
				&:nth-of-type(8){
					opacity: 1;
					transform: translateY(0px);
				}
			}
		}
	}
}

html,
body{
	color: $full_black;
	font-size: 14px;
	font-family: $font_sf;
	font-weight: 300;
	background-color: $full_black;
	//background-image: url('../images/bg_line2.jpg'), url('../images/bg_line1.jpg');
	//background-position:  top center, center;
	//background-repeat: repeat-y, repeat;
	background-image:  
		url('../images/bg_game_png_1__big.png'),
			url('../images/bg_game_png_2__big.png'),
			url('../images/bg_game_png_3__big.png'),
			url('../images/bg_game_png_4__big.png'),
			url('../images/bg_game_png_5__big.png'),
			url('../images/bg_line_repeat.png');

	background-position: 
		50% 9%, 
		50% 25%, 
		50% 45%, 
		50% 62%, 
		50% 80%, 
		center;
	background-repeat: 
		no-repeat,
		no-repeat,
		no-repeat,
		no-repeat,
		no-repeat,
		repeat;
	background-size: 
		contain,
		contain,
		contain,
		contain,
		contain,
		auto;
}

body{
	opacity:0;
	transition: 1s;

	&.show{
		opacity:1;
	}
}

.mobile-menu{
	display: none;
}

header{
	background-image: url('../images/bg_head.jpg');
	background-position: center;
	background-size: cover;
	background-repeat: repeat;
    padding-bottom: 15px;
	border-bottom: solid 1px $color_yellow;
	box-shadow: 0px 2px 6px rgba(229, 177, 47, 0.7);

	.language-box,
	.loginmenu-container{
		display: none;
	}

	>.container{
		position: relative;
		z-index: 3;
	}

	&:before{
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 2;
		display: block;
		background-image: url('../images/bg_head__gold.png');
		background-position: center;
		background-size: cover;
		background-repeat: repeat;
	}

	&:after{
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: calc(100% - 1px);
		z-index: 1;
		display: block;
		background-image: url('../images/bg_head__persons.png');
		background-position: 50% 40%;
		background-repeat: no-repeat;
	    background-size: 1410px;
	}
}

.header__logo{
	position: relative;
	z-index: 1;
	padding-top: 34px;
    padding-bottom: 38px;

	.header__logo-link{
		display: block;
		margin: 0 auto;

		img{
			height: 340px;
		    margin-top: -70px;
		    margin-bottom: -70px;
		}
	}
}

.header__menu{
	position: relative;
	z-index: 2;
    margin-top: 0px;

	.header__menu-container{
		display: block;
		margin: 0 auto;

		.header__menu-box{
			display: inline-block;
			float: left;
			width: auto;
			padding: 0px 19px;

			.header__menu-link{
				font-weight: normal;
				font-size: 16px;
				line-height: 16px;
				text-transform: uppercase;
				font-family: $font_ok;
				color: $color_yellow_hover;
				transition: color 0.3s linear 0s;

				&:hover{
					color: $color_yellow_black;
				}
			}
		}
	}
}

.section-head{
	padding-top: 73px;
	position: relative;
	z-index: 2;


	&_scroll{
		opacity: 0;
		transition: 0.3s;
		transform: translateY(-150px);
		padding-bottom: 13px;
		display: block;
		position: fixed;
		z-index: 100;
		top: 0;
		left: 0;
		width: 100%;
		padding-top: 10px;
		background-image: url('../images/bg_head_scroll.jpg');
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;
		box-shadow: 0px 1px 20px rgba(250, 198, 68, 0.2);

		&.active{
			opacity: 1;
			transform: translateY(0px);	
		}

		.language-container{
			padding-top: 12px;
		}

		.login-container{
		    padding-top: 12px;
		    padding-left: 33px;

			a{
				i{
					font-size: 30px;
				}
			}
		}

		.container-games {
			.games-container{
			    margin: 0 22px;

				a{
					font-size: 54px;
				}
			}
		}

		.container-search {
		    padding-top: 16px;

		    button{
		        top: 13px;
		    }
		}
	}
}

.login-container{
	display: block;
	float: left;
	width: auto;
    padding-top: 25px;
    padding-left: 15px;

    a{
    	display: inline-block;
    	float: left;
    	transition: 0.3s;

    	&:hover{
    		opacity: 0.5;
    	}

    	i,
    	span{
    		display: inline-block;
    		float: left;
    		width: 100%;
    		text-align: center;
    		background-image: url('../images/bg_games.jpg');
			background-size: cover;
			background-position: center;
			-webkit-background-clip:text;
  			-webkit-text-fill-color: transparent;
    	}

    	i{
    		font-size: 21px;
    	}

    	span{
    		font-family: $font_ok;
    		font-size: 10px;
    		text-transform: uppercase;
    		line-height: 10px;
    	}
    }
}

.language-container{
	display: block;
	float: left;
	width: auto;
    padding-top: 24px;
    padding-left: 21px;

	ul{
		display: block;
		float: left;
		width: auto;

		li{
			display: inline-block;
			float: left;
			width: auto;
			background-color: transparent;
			background-image: url('../images/lang_box.png');
			background-position: center;
			background-repeat: repeat;
			background-size: cover;

			&:hover{
				background-image: url('../images/lang_box_hover.png');
				background-color: transparent;
			}

			&.active{
				background-image: url('../images/lang_box__active.jpg');
				background-color: $color_yellow;
				background-position: 100% 50%;

				&:hover{
					background-image: url('../images/lang_box__active_hover.png');
					background-color: transparent;
				}
			}

			a{
				display: inline-block;
				float: left;
				width: auto;
				font-family: $font_ok;
				text-transform: uppercase;
				font-size: 18px;
				color: $full_black;
				line-height: 18px;
				padding: 8px 12px 4px 12px;
				
			}

			&:first-of-type{
				margin-right: 1px;
				border-radius: 3px 0 0 3px;
			}

			&:last-of-type{
				border-radius: 0 3px 3px 0;
			}
		}
	}
}

@media only screen and (min-width: 1160px){
	.container-games{
		.games-container{
			a{
				&:hover{
					background-image: none;
	  				-webkit-background-clip: normal;
	  				-webkit-text-fill-color: $full_black;

	  				&:before{
	  					-webkit-text-stroke-width: 1px;
	  					-webkit-text-stroke-color: $color_yellow;
	  					text-shadow: 0px 0px 8px rgba($color_yellow, 0.6);
					}

					
				}
			}

			&:hover{
				+ .games-container__detail{
						display: block;
				}
			}
		}
	}
}

.container-games{
	padding-left: 0px;

	.games-container{
		margin: 0 31px;

		a{

			display: block;
			margin: 0 auto;
			font-size: 77px;
			color: $color_yellow;
			
			background-image: url('../images/bg_games.jpg');
			background-size: cover;
			background-position: center;
			-webkit-background-clip:text;
  			-webkit-text-fill-color: transparent;

  			&.active-game{
					background-image: none;
	  				-webkit-background-clip: normal;
	  				-webkit-text-fill-color: $full_black;

	  				&:before{
	  					-webkit-text-stroke-width: 1px;
	  					-webkit-text-stroke-color: $color_yellow;
	  					text-shadow: 0px 0px 8px rgba($color_yellow, 0.6);
					}
				}
		}

		&:first-of-type{
			margin-left: auto;
		}

		&:nth-last-child(2){
			margin-right: auto;
		}
	}

	.games-container__detail{
		display: none;
		position: absolute;
		top: calc(100% - 1px);
		z-index: 2;
		left: 40px;
    	width: calc(100% - 50px);
		padding-top: 27px;

		&:hover{
			display: block;
		}

		.games-container__detai-container{
			border-radius: 3px;
			min-height: 100px;
			box-shadow: 0px 0px 10px rgba(250, 198, 68, 0.2);
			padding: 39px 63px 68px 63px;
		}

		&_wow{
			.games-container__detai-container{
				background-image: url('../images/bg_wow.jpg');
				background-position: center;
				background-size: cover;
				background-repeat: no-repeat;
			}
		}

		&_wow-classic{
			.games-container__detai-container{
				background-image: url('../images/bg_wow_classic.jpg');
				background-position: center;
				background-size: cover;
				background-repeat: no-repeat;
			}
		}

		&_dota2{
			.games-container__detai-container{
				background-image: url('../images/bg_dota2.jpg');
				background-position: center;
				background-size: cover;
				background-repeat: no-repeat;
			}
		}
	}
}

.container-search{
    padding-top: 26px;
    padding-right: 19px;
    position: relative;
    z-index: 1;

    form{
    	display: block;
    	float: right;
    }

    .search-result{
    	display: none;
    	z-index: 10;
    	position: absolute;
    	top: calc(100% - 12px);
    	right: 20px;
    	width: 363px;
    	height: auto;
    	min-height: 100px;
    	border-radius: 3px;
    	box-shadow: 0px 0px 10px rgba(250, 198, 68, 0.1);
    	background-image: url('../images/search_result.jpg');
    	background-size: cover;
    	background-position: center;
    	background-repeat: no-repeat;
    	padding: 35px 10px 10px 20px;

    	&.active{
    		display: block;
    	}

    	&:before{
    		content: '';
    		position: absolute;
    		top: 14px;
    		left: 50px;
    		width: calc(100% - 100px);
    		display: block;
    		height: 1px;
    		background-color: rgba($color_yellow, 0.2);
    		box-shadow: 0px 1px 5px rgba(250, 198, 68, 0.4);
    	}

    	&:after{
    		content: '';
    		position: absolute;
    		bottom: 85px;
    		left: 50px;
    		width: calc(100% - 100px);
    		display: block;
    		height: 1px;
    		background-color: rgba($color_yellow, 0.2);
    		box-shadow: 0px 1px 5px rgba(250, 198, 68, 0.4);
    	}



    	.view_more__result{
    		display: inline-block;
    		float: left;
    		height: 48px;
    		font-weight: bold;
			font-size: 18px;
			line-height: 18px;
			text-align: center;
			font-family: $font_ok;
			text-shadow: 0px 0px 10px rgba(250, 198, 68, 0.1);
			background-image: url('../images/bg_button.jpg');
			background-position: center;
			background-repeat: no-repeat;
			border-radius: 3px;
			text-transform: uppercase;
			color: $color_2e;
			margin-top: 21px;
		    margin-left: auto;
		    margin-right: auto;
		    padding: 17px 60px 10px;
		    margin-bottom: 6px;
		    transition: 0.3s;

		    &:hover{
		    	box-shadow: 0px 0px 10px rgba(250, 198, 68, 0.5);
		    }
    	}

    	.search-result__list{
    		max-height: 430px;
    		overflow-y: scroll;
    		overflow-x: hidden;
    		margin-bottom: 12px;

    		&::-webkit-scrollbar-button {
			  background-image: url("");
			  background-repeat: no-repeat;
			  width: 2px;
			  height: 0px; }

			&::-webkit-scrollbar-track {
			  background-color: rgba(250, 198, 68, 0.4);
			  box-shadow: 0px 0px 5px rgba(250, 198, 68, 0.4); }

			&::-webkit-scrollbar-thumb {
			  -webkit-border-radius: 2px;
			  -moz-border-radius: 2px;
			  -ms-border-radius: 2px;
			  border-radius: 2px;
			  border-radius: 0px;
			  background-color: rgba(250, 198, 68, 0.4);
			  box-shadow: 0px 0px 5px rgba(250, 198, 68, 0.4);
			  background-color: $color_yellow_black; }

			&::-webkit-resizer {
			  background-image: url("");
			  background-repeat: no-repeat;
			  width: 2px;
			  height: 0px; }

			&::-webkit-scrollbar {
			  width: 2px; }

    		.search-result__item{
    		    margin-bottom: 20px;
    		    padding-right: 5px;

    			.search-result__item-image{
    				display: block;
    				float: left;
    				border-radius: 3px 0 0 3px;
    				width: 99px;
    				height: 90px;
    				background-size: cover;
    				background-position: center;
    			}

    			.search-result__item-info{
    				width: calc(100% - 145px);
				    display: inline-block;
				    float: left;
				    padding-top: 14px;
				    padding-left: 10px;

    				a{
    					display: inline-block;
    					float: left;
    					width: 100%;
    					font-weight: 300;
						font-size: 14px;
						line-height: 17px;
						color: $color_yellow_hover;
						transition: 0.3s;

						&:hover{
							color: $color_yellow;
						}
    				}
    			}

    			.search-result__item-price{
    				display: block;
    				float: left;
    				width: 45px;
    				font-weight: bold;
					font-size: 20px;
					line-height: 20px;
					text-align: right;
					font-family: $font_ok;
					background-image: url('../images/bg_games.jpg');
					padding-top: 52px;
				    background-size: cover;
				    background-position: center;
				    -webkit-background-clip: text;
				    -webkit-text-fill-color: transparent;
    			}
    		}
    	}
    }

	input{
		margin-left: auto;
		margin-right: 0px;
		border: 0px;
		background-color: transparent;
		color: $color_yellow;
		font-size: 18px;
		font-weight: 300;
	    padding-left: 3px;
	    padding-right: 40px;
	    width: 160px;
		border-bottom: solid 1px $color_yellow;

		&::-webkit-input-placeholder,
		&::placeholder{
			color: $color_yellow !important;
		}
	}

	button{
		display: block;
		position: absolute;
		z-index: 2;
		top: 23px;
		right: 18px;
		border: 0px;
		background-color: transparent;
		cursor: pointer;
		background-image: url('../images/bg_games.jpg');
		background-size: cover;
		background-position: center;
		-webkit-background-clip:text;
  		-webkit-text-fill-color: transparent;

		i{
			display: inline-block;
			float: right;
			font-size: 23px;
			color: $color_yellow;
		}
	}
}

.hot-offers{
    margin-top: 202px;
    margin-bottom: 204px;
}

.hot-offers__slider{
	.owl-item{
		padding: 20px 21px;
	}

	.item{
		box-shadow: 0px 0px 10px rgba($color_yellow, 0.2);
		height: 480px;
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;
		border-radius: 3px;
		position: relative;
	}

	.owl-dots{
		position: absolute;
		bottom: 3px;
		left: 0;
		width: 100%;
		height: 1px;

		.owl-dot{
			span{
				margin: 0 5px;
				background-color: rgba(229, 177, 47, 0.1);
				transition: 0.3s;

				&:hover{
					background-color: rgba(229, 177, 47, 0.5);
				}
			}

			&:hover{
				span{
					background-color: rgba(229, 177, 47, 0.5);
				}
			}

			&.active{
				span{
					background-color: $color_yellow;
					box-shadow: 0px 0px 4px $color_yellow;

					&:hover{
						background-color: $color_yellow;
					}
				}
			}
		}
	}

	.owl-nav{
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 1px;

		div{
			font-size: 0px !important;
			color: transparent !important;
			background-color: transparent !important;
			position: absolute !important;
			top: 4px;
			height: 1px !important;
			margin: 0px !important;
			padding: 0px !important;

			&:hover{
				&:before{
					text-shadow: 0px 0px 4px $color_yellow;
					color: rgba($color_yellow, 1);
				}
			}

			&.owl-prev{
				left: 18px;

				&:before{
					transition: 0.3s;
					content: "\e901";
					display: block;
					position: absolute;
					top: 0;
					left: 0;
					z-index: 2;
					line-height: 10px;
					font-family: 'icomoon' !important;
					color: rgba($color_yellow, 0.7) !important;
					font-size: 70px !important;
				}
			}

			&.owl-next{
				right: 90px;
				top: -1px;

				&:before{
					transition: 0.3s;
					content: "\e902";
					display: block;
					position: absolute;
					top: 0;
					left: 0;
					z-index: 2;
					line-height: 10px;
					font-family: 'icomoon' !important;
					color: rgba($color_yellow, 0.7) !important;
					font-size: 70px !important;
				}
			}
		}
	}

	.hot-offers__slider-text{
		position: absolute;
		bottom: 39px;
	    left: 57px;
	    z-index: 1;
	    max-width: 340px;

		.slider-text__title{
			text-transform: uppercase;
			display: inline-block;
			float: left;
			width: 100%;
			color: $color_yellow;
			font-size: 20px;
			line-height: 20px;
			font-family: $font_ok;
